import { useContext, useState, useEffect } from "react";
import { useNavigate } from 'react-router-dom';
import { Box, Tabs, Tab, TextField, Button, Snackbar, Alert, CircularProgress} from "@mui/material";
import CustomAppBar from "../../components/shared-components/AppBar/AppBar";
import DraftJsWysiwyg from "../../components/blog-components/Draft-js-wysiwyg/Draft-js-wysiwyg";
import BlogService from "../../services/BlogService";
import { SettingsContext } from "../../context/SettingsContext";
import { useUser } from "../../context/UserContext";
import {
  EditorState,
  convertToRaw,
  convertFromRaw,
} from "draft-js";

const AddBlogPage = () => {
  const navigate = useNavigate();
  const { themeMode, language } = useContext(SettingsContext);
  const { userState } = useUser();

  const [activeTab, setActiveTab] = useState(0);
  const [tabsData, setTabsData] = useState({
    CN: { title: "", content: EditorState.createEmpty() },
    EN: { title: "", content: EditorState.createEmpty() },
  });

  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const [snackbarSeverity, setSnackbarSeverity] = useState("success");
  
  // 添加提交状态
  const [isSubmitting, setIsSubmitting] = useState(false);

  // 关闭时触发
  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  // -------------------
  // 1) 组件挂载时，获取草稿
  // -------------------
  useEffect(() => {
    const fetchDraft = async () => {
      try {
        const draft = await BlogService.getDraft(
          userState.username, 
          userState.access
        );
        if (draft) {
          const cnEditorState = draft.cn_content
            ? EditorState.createWithContent(
                convertFromRaw(JSON.parse(draft.cn_content))
              )
            : EditorState.createEmpty();

          const enEditorState = draft.en_content
            ? EditorState.createWithContent(
                convertFromRaw(JSON.parse(draft.en_content))
              )
            : EditorState.createEmpty();

          setTabsData({
            CN: {
              title: draft.cn_title || "",
              content: cnEditorState,
            },
            EN: {
              title: draft.en_title || "",
              content: enEditorState,
            },
          });
        }
      } catch (error) {
        console.error("Error fetching draft:", error);
      }
    };

    fetchDraft();
  }, [userState.access, userState.username]);

  // -------------------
  // 2) 键盘快捷键监听：Ctrl + S 或 Cmd + S
  // -------------------
  useEffect(() => {
    const handleKeyDown = (event) => {
      if ((event.ctrlKey || event.metaKey) && event.key === "s") {
        event.preventDefault();
        handleSaveDraft();
      }
    };

    window.addEventListener("keydown", handleKeyDown);
    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, [tabsData]);

  // -------------------
  // 选项卡切换
  // -------------------
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const handleInputChange = (langKey, field, value) => {
    setTabsData((prev) => ({
      ...prev,
      [langKey]: { ...prev[langKey], [field]: value },
    }));
  };

  const handleContentChange = (langKey, editorState) => {
    setTabsData((prev) => ({
      ...prev,
      [langKey]: { ...prev[langKey], content: editorState },
    }));
  };

  // -------------------
  // 保存草稿逻辑
  // -------------------
  const handleSaveDraft = async () => {
    const draftData = {
      cn_title: tabsData.CN.title,
      en_title: tabsData.EN.title,
      author: userState.username,
      cn_content: JSON.stringify(
        convertToRaw(tabsData.CN.content.getCurrentContent())
      ),
      en_content: JSON.stringify(
        convertToRaw(tabsData.EN.content.getCurrentContent())
      ),
      status: "draft",
    };

    try {
      const response = await BlogService.saveDraft(draftData, userState.access);
      setSnackbarOpen(true);
      setSnackbarMessage(language === "cn" ? "草稿已保存" : "Draft saved");
      setSnackbarSeverity("success");
    } catch (error) {
      setSnackbarOpen(true);
      setSnackbarMessage(language === "cn" ? "保存失败" : "Failed to save draft");
      setSnackbarSeverity("error");
    }
  };

  // -------------------
  // 提交发布新博客
  // -------------------
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // 开始提交，设置loading状态
    setIsSubmitting(true);
    
    const dataToSubmit = {
      cn_title: tabsData.CN.title,
      en_title: tabsData.EN.title,
      author: userState.username,
      cn_content: JSON.stringify(
        convertToRaw(tabsData.CN.content.getCurrentContent())
      ),
      en_content: JSON.stringify(
        convertToRaw(tabsData.EN.content.getCurrentContent())
      ),
      visible: true
    };

    try {
      const response = await BlogService.addBlog(dataToSubmit, userState.access);
      console.log("Blog added successfully:", response);
      
      // 成功后重置输入
      setTabsData({
        CN: { title: "", content: EditorState.createEmpty() },
        EN: { title: "", content: EditorState.createEmpty() },
      });
      
      // 导航到博客列表页
      navigate("/blogs?updated=true");
    } catch (error) {
      console.error("Error adding blog:", error);
      
      // 显示错误提示
      setSnackbarOpen(true);
      setSnackbarMessage(language === "cn" ? "发布失败" : "Failed to publish blog");
      setSnackbarSeverity("error");
    } finally {
      // 无论成功还是失败，都要结束loading状态
      setIsSubmitting(false);
    }
  };

  // -------------------
  // 额外：清空输入内容
  // -------------------
  const handleClear = () => {
    setTabsData({
      CN: { title: "", content: EditorState.createEmpty() },
      EN: { title: "", content: EditorState.createEmpty() },
    });
  };

  // -------------------
  // 多语言标签
  // -------------------
  const labels = {
    title: language === "cn" ? "标题" : "Title",
    content: language === "cn" ? "内容" : "Content",
    submit: language === "cn" ? "提交" : "Submit",
    save: language === "cn" ? "暂存" : "Save",
    clear: language === "cn" ? "清空" : "Clear",
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        height: "100vh", 
        overflow: "hidden",
      }}
    >
      <CustomAppBar />
      <Box
        sx={{
          flex: 1,
          display: "flex",
          flexDirection: "column",
          maxWidth: 1000,
          width: "100%",
          mx: "auto",
          mt: 11  ,
          px: 2,
          overflow: "hidden", 
        }}
      >
        <Tabs
          value={activeTab}
          onChange={handleTabChange}
          sx={{ flexShrink: 0 }}
          variant="fullWidth"
        >
          <Tab label="CN" />
          <Tab label="EN" />
        </Tabs>

        <Box
          component="form"
          onSubmit={handleSubmit}
          sx={{
            display: "flex",
            flexDirection: "column",
            flex: 1,
            overflow: "hidden",
          }}
        >
          <TextField
            label={
              activeTab === 0 
                ? `${labels.title} (CN)` 
                : `${labels.title} (EN)`
            }
            variant="filled"
            value={
              activeTab === 0 
                ? tabsData.CN.title 
                : tabsData.EN.title
            }
            onChange={(e) =>
              handleInputChange(
                activeTab === 0 ? "CN" : "EN",
                "title",
                e.target.value
              )
            }
            fullWidth
            sx={{ 
              flexShrink: 0,
              '& .MuiFilledInput-root': {
                backgroundColor: 'transparent',
                '&:hover': {
                  backgroundColor: 'transparent',
                },
                '&.Mui-focused': {
                  backgroundColor: 'transparent',
                },
              }
            }}
          />

          {/* Editor容器 - 占据剩余空间并可滚动 */}
          <Box
            sx={{
              flex: 1,
              display: "flex",
              flexDirection: "column",
              minHeight: 0, 
            }}
          >
            <DraftJsWysiwyg
              editorState={
                activeTab === 0 
                  ? tabsData.CN.content 
                  : tabsData.EN.content
              }
              onEditorStateChange={(editorState) =>
                handleContentChange(
                  activeTab === 0 ? "CN" : "EN",
                  editorState
                )
              }
            />
          </Box>

          <Box
            sx={{
              display: "flex",
              justifyContent: "flex-end",
              gap: 2,
              mt: 2,
              mb: 2,
              flexShrink: 0,
            }}
          >
            <Button 
              variant="outlined" 
              color="secondary" 
              onClick={handleClear}
              disabled={isSubmitting}
              sx={{ minWidth: 85 }}
            >
              {labels.clear}
            </Button>

            <Button
              variant="outlined"
              color="primary"
              onClick={handleSaveDraft}
              disabled={isSubmitting}
              sx={{ minWidth: 85 }}
            >
              {labels.save}
            </Button>

            <Button 
              type="submit" 
              variant="contained" 
              color="primary"
              disabled={isSubmitting}
              sx={{ minWidth: 90 }}
            >
              {isSubmitting ? <CircularProgress size={20} /> : labels.submit}
            </Button>
          </Box>
        </Box>
      </Box>

      {/* Snackbar + Alert 提示条 */}
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={3000} 
        onClose={handleSnackbarClose}
        anchorOrigin={{ vertical: "top", horizontal: "center" }} 
      >
        <Alert 
          severity={snackbarSeverity} 
          sx={{ width: "100%" }}
        >
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default AddBlogPage;